<template>
  <v-ons-page>
    
      <v-ons-toolbar>
      <div class="center">ランチPWAテスト </div>
    </v-ons-toolbar>
    <v-ons-list>
        <v-ons-list-item v-for="post in posts" :key="post.id">
                    <div class="left">
                       <router-link v-bind:to="{ path : 'detail', query : { id: post.id }}">
                      <img class="list-item__thumbnail" v-bind:src="post.acf.imagefield.url">
                      </router-link>
                    </div>
                    <div class="center">
                      <span class="list-item__title"><router-link v-bind:to="{ path : 'detail', query : { id: post.id }}">{{post.title.rendered}} </router-link></span>
                      <span class="list-item__subtitle"><router-link v-bind:to="{ path : 'detail', query : { id: post.id }}">{{post.acf.access.address}}</router-link></span>
                    </div>
        </v-ons-list-item>
    </v-ons-list>
  </v-ons-page>
</template>

<script>
import axios from 'axios';
import Vue from 'vue'


export default {
  name: 'HomePage',
  data () {
    return {
      msg: 'Welcome',
      posts: []
    }
  },
  methods: {
    goTo (url) {
      const newWindow = window.open(url, '_blank')
      newWindow.opener = null
      newWindow.location = url
    }
  },
  created(){
      axios.get ('https://code.hot-factory.jp/lunch/wp-json/wp/v2/store/')
        .then ((response) => {
            this.posts = response.data;
            // console.log(this.renderItem.post_content.title);
        }).catch( ( res ) => {
                    console.error( res );
        });

    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



