<template>
  <v-ons-page>
    <v-ons-toolbar modifier="transparent"></v-ons-toolbar>
    <div class="header">
      <img src="../assets/onsenui-logo.png">
    </div>

    <v-ons-list-title>Onsen UI Essential Links</v-ons-list-title>
    <v-ons-list>
  <v-ons-list-item modifier="chevron" v-for="item in essentialLinks" @click="onClick (item.link)" :key="item.link" @toggleMenu="menuIsOpen = !menuIsOpen">
    <div class="left"><v-ons-icon fixed-width :icon="item.icon"></v-ons-icon></div>
    <div class="center">{{ item.label }}</div>
  </v-ons-list-item>
</v-ons-list>
  </v-ons-page>
</template>

<script>
export default {
  name: 'menu',
  data () {
    return {
      essentialLinks: [
        { label: 'HomePage', link: 'HomePage', icon: 'fa-book' },
        { label: 'DetailPage', link: 'DetailPage', icon: 'fa-book' },
        { label: 'SignUp', link: 'SignUp', icon: 'fa-book' },
        { label: 'EditUserData', link: 'EditUserData', icon: 'fa-book' }
        
        
      ]
    }
  },
  methods: {
    onClick (link) {
      this.$emit('pageName', link);
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
  margin-bottom: 20px;
}

img {
  max-width: 150px;
}

ons-list-title {
  text-transform: none;
}

ons-list-item {
  cursor: pointer;
}
</style>


