import Vue from 'vue';
import VueRouter from 'vue-router';
import HomePage from './pages/HomePage';
import DetailPage from './pages/DetailPage';
import routes from './routes'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
        {
        path: '/',
        name: 'HomePage',
        component: HomePage
        },
        {
        path: '/detail',
        name: 'DetailPage',
        component: DetailPage
        }
    ]
})


