<template>
  <v-ons-page>
      <v-ons-toolbar>
    <div class="left"><router-link to="/">Back</router-link></div>
      <div class="center"> {{this.posts.title.rendered}} </div>
    </v-ons-toolbar>

<v-ons-card>
      <img v-bind:src="this.posts.acf.imagefield.url" alt="Onsen UI" style="width: 100%">
      <div class="title">
         {{this.posts.title.rendered}} 
      </div>
      <div class="content">
        
        <v-ons-list>
        <v-ons-list-header>Detail</v-ons-list-header>
            <v-ons-list-item>
                <div class="left">住所</div>
                <div class="right">{{this.posts.acf.access.address}}</div>
            </v-ons-list-item>
            <v-ons-list-item>
                <div class="left">TEL</div>
                <div class="right">{{this.posts.acf.tel}}</div>
            </v-ons-list-item>
            <v-ons-list-item>
                <div class="left">休日</div>
                <div class="right">{{this.posts.acf.holiday}}</div>
            </v-ons-list-item>
            <v-ons-list-item>
                <div class="left">営業時間</div>
                <div class="right">{{this.posts.acf.start_time}}〜{{this.posts.acf.end_time}}</div>
            </v-ons-list-item>
            
          
        
        </v-ons-list>
      </div>
    </v-ons-card>


                      
                     
                     
  </v-ons-page>
</template>

<script>
import axios from 'axios';
import Vue from 'vue'


export default {
  name: 'DetailPage',
  data () {
    return {
      msg: 'Welcome',
      posts: []
    }
  },
  methods: {
    
  },
  created(){
      axios.get ('https://code.hot-factory.jp/lunch/wp-json/wp/v2/store/'+this.$route.query.id)
        .then ((response) => {
            this.posts = response.data;
            console.log(this.posts);
        }).catch( ( res ) => {
                    console.error( res );
        });
        console.log(this.$route.query.id);
        
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



